import React, { useState, useEffect, useRef } from "react";
import AnimatedGrid from "./AnimatedGrid";
import { db, ref, onValue, set } from "./firebase";

const App = () => {
  const [text, setText] = useState("");
  const [submittedText, setSubmittedText] = useState("");
  const [overlayVisible, setOverlayVisible] = useState(true);
  const [lastInteraction, setLastInteraction] = useState(Date.now());
  const idleTimeout = 5 * 60 * 1000; // 5 perc
  const appRef = useRef(null);

  const [timeLevel, setTimeLevel] = useState(7);
  const [outsideLevel, setOutsideLevel] = useState(1);
  const [feelingLevel, setFeelingLevel] = useState(4);
  const [insideLevel, setInsideLevel] = useState(2);
  
  useEffect(() => {
    if (appRef.current) {
      appRef.current.focus();
    }
  }, []);
  // Inaktivitás figyelés
  
  useEffect(() => {
    const handleInteraction = () => {
      setLastInteraction(Date.now());
      setOverlayVisible(false);
    };

    window.addEventListener("mousemove", handleInteraction);
    window.addEventListener("keydown", handleInteraction);

    return () => {
      window.removeEventListener("mousemove", handleInteraction);
      window.removeEventListener("keydown", handleInteraction);
    };
  }, []);

  // 5 perc után újra mutatja az overlay-t
  useEffect(() => {
    const interval = setInterval(() => {
      if (Date.now() - lastInteraction > idleTimeout) {
        setOverlayVisible(true);
      }
    }, 1000);

    return () => clearInterval(interval);
  }, [lastInteraction]);
  useEffect(() => {
    const textRef = ref(db, "sharedText");
  
    // hallgató figyel Firebase-re
    onValue(textRef, (snapshot) => {
      const val = snapshot.val();
      if (val) {
        setSubmittedText(val); // frissíti a megjelenített szöveget
      }
    });
  }, []);
  
  const handleKeyDown = (e) => {
    if (e.key.length === 1) {
      setText((prev) => prev + e.key);
    } else if (e.key === "Backspace") {
      setText((prev) => prev.slice(0, -1));
    } else if (e.key === "Enter") {
      setSubmittedText(text);
      set(ref(db, "sharedText"), text); // elmenti Firebase-be
    }
  };

  return (
    <div
      ref={appRef}
      tabIndex={0}
      onKeyDown={handleKeyDown}
      style={{ backgroundColor: "black", color: "white", overflow: "hidden" }}
    >
      {/* Overlay szöveg */}
      {overlayVisible && (
        <div
          style={{
            position: "absolute",
            top: 0,
            left: 0,
            width: "100vw",
            height: "100vh",
            backgroundColor: "black",
            color: "white",
            display: "flex",
            justifyContent: "center",
            alignItems: "center",
            fontSize: "24px",
            flexDirection: "column", 
            textAlign: "center",
            fontFamily: "monospace",
            zIndex: 9999,
            pointerEvents: "none", // nem akadályozza az interakciót
            gap: "16px",
          }}
        >
           <div>Information is available, yet inaccessible.</div>
           <div>Reception and processing are not givens, but struggles —</div>
           <div>in a world not built for your nervous system.</div>
           <div>Say what’s hard to put into words:</div>
        </div>
      )}

      {/* Tartalom */}
      <section style={{ minHeight: "100vh", overflow: "hidden" }}>
        <AnimatedGrid
          text={submittedText}
          timeLevel={timeLevel}
          outsideLevel={outsideLevel}
          feelingLevel={feelingLevel}
          insideLevel={insideLevel}
        />
      </section>
    </div>
  );
};

export default App;